
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { getTradelist } from 'api'
export default {
data () {
return {
blk: this.$route.query.args,
acc: this.$route.query.acc,
list: [],
page: 1,
noData: true,
loading: false
}
},
created () {
this.getList()
},
beforeRouteUpdate (to, from, next) {
this.blk = to.query.args
this.acc = to.query.acc
this.page = 1
this.list = []
this.noData = true
this.loading = false
this.getList()
next()
},
computed: {
num () {
return this.list.length || '0'
}
},
methods: {
getList () {
if (this.blk) {
this.getByBlock()
} else {
this.getByAcc()
}
},
getRecord (item, type) {
let acc
if (this.acc) {
acc = type === 'facc' ? item.out : item.in
} else {
acc = type === 'facc' ? item.facc : item.tacc
}
if (acc && acc !== this.acc) {
this.$router.push({name: 'BlockRecord', query: {acc}})
}
},
fee (item) {
if (this.acc) {
return item.fee
} else {
let {black, nMinerFee, sMinerFee} = item
let total = Number(black) + Number(nMinerFee) + Number(sMinerFee)
return total
}
},
getStatus (item) {
let status = ''
switch (item.status) {
case '0':
status = '处理中'
break
case '1':
status = '交易成功'
break
case '2':
status = '交易失败'
break
default:
status = '处理中'
break
}
return status
},
getByBlock () {
getTradelist({method: 'qryTxByBlk', blk: this.blk}).then(res => {
this.list = res.data
})
},
getByAcc () {
let args = {
method: 'history',
acc: this.acc,
page: this.page,
number: 20
}
this.loading = true
getTradelist(args).then(res => {
let list = res.data
if (this.page <= 1) {
this.list = list
} else {
this.list.push(...list)
}
this.noData = (list.length < 10)
this.loading = false
})
.finally(() => {
this.loading = false
})
},
loadMore () {
if (this.acc) {
this.noData = true
this.page += 1
this.getByAcc()
}
}
},
components: {
XHeader,
LoadMore
}
}



